@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

h1 {
  text-align: center;
  padding: 10px 0;
}

.Photo__list {
  max-width: 1200px;
  margin: 40px auto 0 auto;
  padding: 0 10px;
}

.Photo__list ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Photo__list ul li {
  display: flex;
  margin-bottom: 30px;
  cursor: pointer;
  position: relative;
  transition: 2s;
  width: 300px;
  height: 200px;
}

.Photo__list ul li:hover::before {
  content: "";
  width: 30px;
  height: 30px;
  background-image: url(/AvitoTask_1/static/media/zoom.faaa8407.svg);
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.Photo__list ul li:hover::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .6);
  width: 100%;
  height: 100%;
}
@keyframes lds-eclipse {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes lds-eclipse {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.lds-eclipse {
  position: relative;
  height: 100%;
  width: 100%;
  margin: auto;
}

.lds-eclipse div {
  position: absolute;
  -webkit-animation: lds-eclipse 1s linear infinite;
  animation: lds-eclipse 1s linear infinite;
  width: 120px;
  height: 120px;
  top: 40px;
  left: 40px;
  border-radius: 50%;
  box-shadow: 0 4px 0 0 #5e6fa3;
  transform-origin: 60px 62px;
}

.lds-eclipse {
  width: 200px !important;
  height: 200px !important;
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}
